.App {
  text-align: center;
}

.black-nav {
  display: flex;
  background: black;
  width: 100%;
  color: white;
  padding-left: 20px;
}
div {
  box-sizing: border-box;
}
.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.modalscreen {
  margin-top: 20px;
  padding: 20px;
  background: lightgrey;
  text-align: left;
  /* width: 80%; */
  text-align: center;
}


.liveUser p{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13px;
  margin-top: 5px;
}

.titleclosebox{
  display: flex;
  justify-content: space-between;
}
.titleclosebox p{
  margin-right: 40px;
  margin-top: 20px;
}